@import "../../globalSASS/global.scss";
.button {
    width: 270px;
    height: 69px;

    font-size: 20px;
    font-weight: 700;
    letter-spacing: 1px;
    color: #132132;

    border: none;
    border-radius: 10px;
    background-color: #f69c1c;

    cursor: pointer;
    outline-style: none;

   &:hover {
    box-shadow: 3px 3px 10px rgba(246, 185, 28, 0.66);
   }

   &:active {
       background-color: #FF9600;
   }

    &_header {
        margin-top: 50px;
    }

    &_mount-form {
        position: absolute;
        bottom: 0;
        right: 40px;

        width: 231px;
        height: 57px;
    }

    &_feedback-form {
        width: 383px;
        height: 79px;

        font-size: 24px;

        margin-top: 35px;
    }

    &_thanks {
        width: 268px;
        height: 67px;

        font-family: Montserrat,serif;
        font-weight: 700;
        font-size: 24px;
        
        margin-top: 46px;

    }
}