@import "../../globalSASS/global.scss";
.popup {

	&__mask {
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.5);
		position: fixed;
        top: 0px;
        backdrop-filter: blur(10px);
        
        z-index: 99;
    }
    
    &__close {
        position: absolute;
        top: 16px;
        right: 20px;

        height: 61px;
        width: 61px;

        cursor: pointer;
    }
}
