@import "../../globalSASS/global.scss";
.mouse-parallax {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    z-index: -10;

    &__wrap {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;

        width: 100%;
        height: 100%;

    }
    &__bg {
        position: absolute;
        width: 110%;
        height: 110%;
        background-position: center center;
        background-size: cover;

        &_couple {
            background-image: url('img/couple.png');
            z-index: -1;
        }

        &_forest {
            background-image: url('img/forest.png');
            z-index: -2;
        }

        &_mountains {
            background-image: url('img/mounts.png');
            z-index: -3;
        }
    }
}
