@import "../../globalSASS/global.scss";
.thanks {
    
    &__wrap {
        position: fixed;
        top: calc(50% - 165px);
        left: calc(50% - 198px);

        display: flex;
        align-items: center;
        justify-content: center;

        width: 396px;
        height: 330px;

        background-color: #fff;
        border-radius: 10px;

        z-index: 100;
        
    }

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 270px;
        height: 230px;
    }

    &__title {
        font-size: 36px;
        margin: 0;
    }

    &__subtitle {
        font-size: 24px;
        text-align: center;
        margin: 0;
        margin-top: 10px;
    }

    
}